<template>
  <div>
    <pre>
      var head = document.querySelector('head')
      var url = "xx.css"
      var link = document.createElement('link')
      link.href = url
      link.setAttribute('rel', 'stylesheet')
      link.setAttribute('type', 'text/css')
      head.appendChild(link)
    </pre>

    <table class="table mb-20">
      <thead>
        <tr><th colspan="2">常用link标签</th></tr>
      </thead>
      <tbody v-for="item in commonList">
        <tr><td>{{item}}</td></tr>
      </tbody>
    </table>

    <table class="table">
      <thead>
        <tr><th colspan="2">常用link标签</th></tr>
      </thead>
      <tbody v-for="item in iosList">
        <tr><td>{{item.key}}</td></tr>
        <tr><td>{{item.value}}</td></tr>
      </tbody>
    </table>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        commonList: [
          '<link rel="stylesheet" type="text/css" href="" />',
          '<link rel="shortcut icon" href="xx.ico" type="images/x-icon"/>',
          '<link rel="shortcut icon" href="xx.png" type="images/png"/>',
          '<link rel="stylesheet" type="text/css" href="print.css" media="print"/>',
          '<link rel="stylesheet" type="text/css" href="xx.css"  media="screen and (max-width: 480px)"/>',
          '<link rel="stylesheet" type="text/css" href="xx.css"  media="screen and (-webkit-device-pixel-ratio: 1.5)/>'
        ],

        iosList: [
          {
            key: '<link rel="apple-touch-icon" href="apple-touch-icon-iphone.png" />',
            value: `在iPhone,iPad,iTouch的safari上可以使用添加到主屏按钮将网站添加到主屏幕上。
            这是IOS设备的私有标签,如果设置了相应apple-touch-icon标签,则添加到主屏上的图标会使用指定的图片`
          },
          {
            key: '<link rel="apple-touch-icon" sizes="72x72" href="apple-touch-icon-ipad.png" />',
            value: `57×57（默认值）的图标对应320×640的老设备，72×72对应ipad，114×114对应retina屏的iPhone及iTouch`
          },
          {
            key: '<link rel="apple-touch-icon-precomposed" href="apple-touch-icon-144x144-precomposed.png" />',
            value: `图片无需做圆角处理,同Native App一样,系统会自动为图标添加圆角及高光。
            如果不想系统对图标添加效果,可以用apple-touch-icon-precomposed代替apple-touch-icon。`
          },
          {
            key: `<link rel="apple-touch-startup-image" media="screen and (orientation: portrait)"
             sizes="640x1136" href="/splash-screen-640x1136.png"/>`,
            value: `portrait: 竖屏,landscape: 横屏。webapp 从主屏打开时,会有一张封面图片。但是被声明的图片
            对于iphone和itouch 大小只能是 320 x 460 ，其他大小的都无效。但是同样你可以通过sizes 来进行多设备适配`
          }
        ]
      }
    }
  }
</script>

<style lang="scss" scoped>
  .table {
    tr:nth-child(2n + 1) {
      background: #efefef;
      color: #42b983;
    }
  }
</style>
